.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background: linear-gradient(to right, #45a0ce, #bee1f2); /* Blue gradient */
}


/* Center the entire section */
.members-container {
  text-align: center;
  padding: 20px;
  background: linear-gradient(to right, #45a0ce, #bee1f2); /* Blue gradient */

}

.members-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px;
}

.member-card {
  background-color: rgb(201, 236, 245);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
  width: 250px;  /* Set a fixed width */
  height: 300px; /* Set a fixed height for uniform size */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.member-card:hover {
  transform: scale(1.05);
}

.member-card h3 {
  margin: 0;
  font-size: 22px;
  color: #007bff;
}

.member-card h4 {
  margin: 5px 0;
  font-size: 18px;
  color: #555;
}

.member-card p {
  font-weight: bold;
  margin: 10px 0 5px;
}

.member-card ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  flex-grow: 1; 
}

.member-card ul li {
  background: #e3e3e3;
  padding: 5px;
  border-radius: 5px;
  margin: 3px 0;
  font-size: 14px;
  text-align: center;
}

.member-card h3,
.member-card h4,
.member-card p {
  text-align: center;
}

.nav-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 30px; /* Space between items */
  margin-top: 40px;
}

.nav-item {
  width: 30%;
  background-color: #007bff;
  color: white;
  text-align: center;
  border-radius: 10px;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.nav-item:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

h1{
  text-align: center;
}
